import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';

@Injectable({
  providedIn: 'root'
})
export class QuizService {
  quiz: any;
  constructor(private http: Http) { }

  getAll() {
    return this.http.get("/admin/quiz").map(res => res.json());
  }
  getQuiz() {
    return this.http.get("/admin/quiz/:id").map(res => res.json());
  }
  addQuiz(quiz) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    console.log('quiz Body ::: ', quiz);
    return this.http.post("localhost:3000/admin/addquiz", quiz, { headers: headers }).map((res) => {
      console.log(res.json());
      return res.json();
    });
  }
}
