import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  firstName: String;
  lastName: String;
  //name: String;
  //username: String;
  emailID: String;
  password: String;
  location: String;
  mobileNo: Number;
  role: String;
  constructor(private validateService: ValidateService,
    private flashMessage:FlashMessagesService,
    private authService: AuthService,
    private router: Router) { }

  ngOnInit() {
  }
  onRegisterSubmit() {
    const user = {
      firstName: this.firstName,
      lastName: this.lastName,
     //username: this.username,
      emailID: this.emailID,
      password: this.password,
      location: this.location,
      mobileNo: this.mobileNo,
      role: this.role
    }
 //Required fields
 if(!this.validateService.validateRegister(user)) {
  this.flashMessage.show('Please fill in all fields', {cssClass: 'alert-danger', timeout: 3000});
  return false; 
}
//validATE email
if(!this.validateService.validateEmail(user.emailID)) {
  this.flashMessage.show('Please provide valid email address', {cssClass: 'alert-danger', timeout: 3000});
  return false;
}

//RegisterUser
this.authService.registerUser(user).subscribe(data => {
  if(data.success){
    this.flashMessage.show('You are now registered and can login', {cssClass: 'alert-success', timeout: 3000});
    console.log('success');     
    this.router.navigate(['/login']);
  } else {
    this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});     
    this.router.navigate(['/register']);
  }
});
}
}
